p {
  margin-top: 0;
  margin-bottom: 1rem;
}

a {
  color: #007bff;
  text-decoration: none;
}

.App {
  padding: 25px;
  display: flex;
  justify-content: center;
  align-items: flex-start;
}

.Card {
  width: 18rem;
  position: relative;
  display: flex;
  flex-direction: column;
  min-width: 0;
  background-color: #fff;
  border: 1px solid rgba(0,0,0,.125);
  border-radius: .25rem;
}

.Card:nth-child(2n + 2) {
  margin-left: 15px;
}

.Card__img_top {
  width: 100%;
  border-top-left-radius: calc(.25rem - 1px);
  border-top-right-radius: calc(.25rem - 1px);
}

.Card__body {
  padding: 1.25rem;
}

.Card__title {
  margin-top: 0;
  margin-bottom: .75rem;
  line-height: 1.2;
  font-size: 1.25rem;
  font-weight: 500;
}

.btn {
  display: inline-block;
  font-weight: 400;
  text-align: center;
  vertical-align: middle;
  border: 1px solid transparent;
  padding: .375rem .75rem;
  font-size: 1rem;
  line-height: 1.5;
  border-radius: .25rem;
}


.btn__primary {
  color: #fff;
  background-color: #007bff;
  border-color: #007bff;
}

.btn__primary:hover {
  color: #fff;
  background-color: #0069d9;
  border-color: #0062cc;
}

.Card__link+.Card__link {
  margin-left: 1.25rem;
}

.Card__link:hover {
  color: #0056b3;
}
